import { Component, OnInit, OnDestroy, TemplateRef } from '@angular/core';
import { Observable } from "rxjs";
import { ApiService } from '../../api.service';
import { Subject } from 'rxjs';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-news-category',
  templateUrl: './news-category.component.html',
  styleUrls: ['./news-category.component.css']
})
export class NewsCategoryComponent implements OnInit {
	dtOptions: DataTables.Settings = {};
	dtTrigger = new Subject();
  	private baseURL = "https://malang-post.com";
  	categorys$: Observable<any>;
  	categories: any = [];
  	items: any = [];
  	modalRef: BsModalRef;
  	message: any = '';
  	selectedItem: any = {};
  	modalType: any = "";
  constructor(
  	public api: ApiService,
  	public modalService: BsModalService,
  	) { }

  ngOnInit(): void {
  	this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 5
    };
  	this.api.provideRestWP(`${this.baseURL}/wp-json/wp/v2/categories?per_page=50`).subscribe(res => {
      this.items = res;
      console.log(this.categories);
      this.dtTrigger.next();

       });

  	this.getCategories();
  	
  }

  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }

  getCategories(){
  	this.categories = [];
  	this.api.provideData({ type: 'getnewscategoriesadm' },'data-adm-api').subscribe((resp)=>{
  		this.categories = resp;

  	});
  }

  addCategories(item){
  	let order = parseInt(this.categories.length)+1;
  	this.api.provideData({
	  		type: 'createnewscategoriesadm',
	  		cat_wp_id: item.id,
	  		cat_wp_link: item.link,
	  		cat_name: item.name,
	  		cat_order: order
	  	}, 'data-adm-api').subscribe((resp)=>{
	  		this.getCategories();
	  	});
  }
  deleteCategories(item){
  	this.api.provideData({
	  		type: 'deletenewscategoriesadm',
	  		cat_id: item.cat_id,
	  	}, 'data-adm-api').subscribe((resp)=>{
	  		this.getCategories();
	  	});
  }

  openModal(template: TemplateRef<any>, item) {
    this.modalRef = this.modalService.show(template, {class: 'modal-sm'});
    this.modalType = "insert";
    this.selectedItem = item;
  }
  openModalDel(template: TemplateRef<any>, item) {
    this.modalRef = this.modalService.show(template, {class: 'modal-sm'});
    this.modalType = "delete";
    this.selectedItem = item;
  }

  confirm(): void {
    this.message = 'Confirmed!';
    if(this.modalType=='insert'){
    	this.addCategories(this.selectedItem);
    }else if(this.modalType=='delete'){
		this.deleteCategories(this.selectedItem);
    }
    
    this.modalRef.hide();
  }
 
  decline(): void {
    this.message = 'Declined!';
    this.modalRef.hide();
  }

  
}
